 body {
	font-family: Verdana;
	margin: 0;
}

body * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.app {
	height: 100vh;
	overflow-y: hidden;
	display: -ms-flexbox;
	display: flex;
}

.canvas {
	width: 100%;
    height: 100%;
    cursor: crosshair;
}

/*
    NavBar
*/

.nav-bar {
    background: #59646e;
}

.nav-bar__tool {
    width: 40px;
    height: 40px;
    padding: 5px 3px;
    margin: 2px;
}

.nav-bar__tool > svg {
	width: 100%;
    height: 100%;
    -webkit-filter: invert(100%);
            filter: invert(100%);
}

.selected {
    
    background: #b5226e;
    width: 40px;
    height: 40px;
    padding: 5px 5px;

}

/*
Tool Panels
*/

.tool-panel {
    background: #eff0f3;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: 222px;
    
}


.line-types input[type="radio"]{
    display:none; 
}

.line-types label {
    display:inline-block;
    background-color:#ddd;
    padding: 5px 9px;
    font-family:Arial;
    font-size:16px;
    margin: 1px;

 
}

.line-types label:hover {   
    background-color:lightgray;
    }

.line-types input[type="radio"]:checked + label { 
    color:white;
    background-color:rgb(0, 133, 221);
}










